import React, { useState } from 'react';
import { reduxForm, InjectedFormProps, Form, Field, FormSubmitHandler } from 'redux-form';
import { useHistory } from 'react-router-dom';
import { withFirebase } from 'components/Firebase';
// import 'styles/form.css';
import Input from 'components/Form/Input/Input';
import { WithFirebase } from 'components/Firebase/FirebaseHOC';
import { required, validEmail, minLength6 } from 'lib/validation';
import { Submit } from 'pages/SignUp';

interface SubmitValues {
  email: string;
  password: string;
}

interface OwnProps extends InjectedFormProps<SubmitValues>, WithFirebase {}

const SignIn: React.FC<OwnProps> = (props) => {
  const history = useHistory();
  const { firebase, handleSubmit } = props;

  const onSubmit: FormSubmitHandler<SubmitValues> = async (values) => {
    // e.preventDefault();
    // try {
    //   const response = await firebase.logIn(email, password);
    // } catch {}
    console.log(values);
    history.push('/signUp');
  };

  return (
    <div className="layout">
      <div className="form-wrapper">
        <h1>Log In</h1>
        <Form onSubmit={handleSubmit(onSubmit)} noValidate>
          <Field name="Email" component={Input} type="email" validate={[required, validEmail]} />
          <Field
            name="Password"
            component={Input}
            type="password"
            validate={[required, minLength6]}
          />
          <Submit type="submit" className="submit ">
            Submit
          </Submit>
        </Form>
        {/* <form onSubmit={onSubmit}>
          <input type="email" value={email} onChange={(e) => setEmail(e.target.value)} />
          <input type="password" value={password} onChange={(e) => setPassword(e.target.value)} />
          <button type="submit">Log In</button>
        </form> */}
      </div>
    </div>
  );
};

export default React.memo(
  reduxForm<SubmitValues>({ form: 'log-in' })(withFirebase(SignIn)),
);
