import { CommonActionTypes, CommonReducerState } from 'domain/common';
import { SET_USER } from './selectors';

const initialState = {
  user: null,
};

export function commonReducer(state = initialState, action: CommonActionTypes): CommonReducerState {
  switch (action.type) {
    case SET_USER:
      return {
        user: action.paylaod,
      };
    default:
      return state;
  }
}
