import React, { useState } from 'react';
import { withFirebase } from 'components/Firebase';
import { WithFirebase } from 'components/Firebase/FirebaseHOC';
import 'styles/form.css';
import { Field, reduxForm, InjectedFormProps, Form, FormSubmitHandler } from 'redux-form';
import Input from 'components/Form/Input/Input';
import { required, minLength6, validEmail } from 'lib/validation';
import styled from 'styled-components';
import { useHistory } from 'react-router-dom';

export const Submit = styled.button`
  margin-top: 50px;
  outline: none;
  background-color: transparent;
  border: none;
  border-radius: 20px;
  cursor: pointer;
  padding: 10px 40px;
  font-size: 20px;
  font-family: 'Poppins';
  font-weight: 500;
  transition: box-shadow 0.15s ease-in;
  &:hover,
  &:focus {
    box-shadow: 0px 1px 5px 1px gray;
  }
`;

interface SubmitValues {
  email: string;
  password: string;
}

interface OwnProps extends InjectedFormProps<SubmitValues>, WithFirebase {}

const SignIn: React.FC<OwnProps> = (props) => {
  const { firebase, handleSubmit } = props;
  const history = useHistory();

  const onSubmit: FormSubmitHandler<SubmitValues> = async (values, _, props) => {
    // @ts-ignore
    // values.preventDefault();
    // console.log(values, props);
    // e.preventDefault();
    // try {
    //   if (firebase) {
    //     const response = await firebase.signUp(email, password);
    //   }
    // } catch {}
    // console.log(history);
    console.log(values);
    history.push('/logIn');
  };

  return (
    <div className="layout">
      <div className="form-wrapper">
        <h1>Sign Up</h1>
        <Form onSubmit={handleSubmit(onSubmit)} noValidate>
          <Field name="Email" component={Input} type="email" validate={[required, validEmail]} />
          <Field
            name="Password"
            component={Input}
            type="password"
            validate={[required, minLength6]}
          />
          <Submit type="submit" className="submit ">
            Submit
          </Submit>
        </Form>
      </div>
    </div>
  );
};

export default React.memo(
  reduxForm<SubmitValues>({ form: 'sign-up' })(withFirebase(SignIn)),
);
